									<input type='number' class="colorTxt fz24 pl18" placeholder="请输入转账金额" placeholder-class="phcolor" />
									<!-- <image class="successIcon" src="../../../static/right_icon.png"></image> -->
									<label class="allMoney">全部</label>
								</view>

							</view>
